<template>
  <div id="a">
    <div>
      <el-input
        style="width: 200px"
        placeholder="姓名"
        v-model="name"
      ></el-input>
      <el-select v-model="gender" placeholder="性别" style="margin: 0 10px">
        <el-option
          v-for="item in options"
          :key="item.gender"
          :label="item.label"
          :value="item.gender"
        >
        </el-option>
      </el-select>
      <el-button type="primary" @click="load(1)">查询</el-button>
    </div>
    <div style="margin: 10px 0">
      <el-button type="success" plain>新增</el-button>
      <el-button type="danger" plain>批量删除</el-button>
    </div>
    <el-table
      :data="emps"
      border
      stripe
      :header-cell-style="{ backgroundColor: 'aliceblue', color: '#666' }"
      :height="334"
    >
      <el-table-column
        type="selection"
        width="55px"
        align="center"
      ></el-table-column>
      <el-table-column
        label="用户名"
        align="center"
        prop="username"
      ></el-table-column>
      <el-table-column
        label="姓名"
        align="center"
        prop="name"
      ></el-table-column>
      <el-table-column
        label="性别"
        align="center"
        prop="genderName"
      > 
      </el-table-column>
      <el-table-column label="职位" align="center" prop="job"></el-table-column>
      <el-table-column label="操作" align="center" width="180px">
        <el-button type="success" size="small" plain>编辑</el-button>
        <el-button type="danger" size="small" plain>删除</el-button>
      </el-table-column>
    </el-table>
    <div style="margin: 10px 0">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="pagesize"
        layout="total,prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog title="收货地址" :visible.sync="formVisible">
      <div slot="footer" class="dialog-footer">
        <el-button @click=" formVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
 
<script>
import axios from "axios";

export default {
  name: "P2View",
  data() {
    return {
      emps: [],
      page: 1,
      pagesize: 5,
      name: "",
      total: 0,
      formVisible: false,
      options: [
        {
          gender: "1",
          label: "男",
        },
        {
          gender: "2",
          label: "女",
        },
      ],
      gender: "",
    };
  },
  created() {
    this.load();
  },
  methods: {
    load(page) {
      if (page) {
        this.page = page;
      }
      axios
        .get("/api/emps", {
          params: {
            page: this.page,
            pageSize: this.pagesize,
            name: this.name,
            gender: this.gender,
          },
        })
        .then((res) => {
          console.log(res);
          this.emps = res.data.data.rows;
          this.emps.forEach(item => {
            if(item.gender == 1) {
              item.genderName = "男"
            } else {
              item.genderName = "女"
            }
          })
          this.total = res.data.data.total;
        });
    },
    handleCurrentChange(page) {
      this.page = page;
      this.load(page);
    },
  },
};
</script>

<style scoped>
#a {
  margin: 10px;
}
</style>